@font-face {
  font-family: NSABold;
  src: url("../fonts/NotoSansArmenian-Bold.ttf");
}

@font-face {
  font-family: NSABold;
  src: url("../fonts/NotoSansArmenian-Bold.ttf");
}

@font-face {
  font-family: NSARegular;
  src: url("../fonts/NotoSansArmenian-Regular.ttf");
}

@font-face {
  font-family: NSAMedium;
  src: url("../fonts/NotoSansArmenian-Medium.ttf");
}

@font-face {
  font-family: NSALight;
  src: url("../fonts/NotoSansArmenian-Light.ttf");
}

@font-face {
  font-family: NSAThin;
  src: url("../fonts/NotoSansArmenian-Thin.ttf");
}

body {
  font-family: NSARegular, "Roboto" sans-serif;
}

body::-webkit-scrollbar {
  width: 5px;
  height: 7px;
  overflow: hidden;
}

body::-webkit-scrollbar-track {
  background: #ccc;
}

body::-webkit-scrollbar-thumb {
  background: #e3774c;
}

p {
  line-height: 22px;
}

a {
  color: #4088fc;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
  color: #e3774c;
}

@media screen and (max-width: 768px) {
  nav .container .nav_menu .menu_btn {
    display: flex;
  }

  nav .container .nav_menu ul {
    position: absolute;
    flex-flow: column;
    justify-content: flex-start;
    top: 0px;
    width: 500px;
    overflow: hidden;
    background: #d7d7e2;
    transition: left 0.2s linear;
    left: -5000px;
    padding: 0 25px;
  }

  nav .container .nav_menu ul li a {
    height: 50px;
    padding: 8px 20px;
  }

  nav .container .nav_menu ul.active_nav {
    height: 100vh;
    left: 0;
  }
}

@media screen and (max-width: 768px) and (max-width: 580px) {
  nav .container .nav_menu ul {
    width: 300px;
  }
}

@media screen and (max-width: 768px) and (max-width: 420px) {
  nav .container .nav_menu ul {
    width: 200px;
  }
}